<template>
<div class="cart">
    <div class="cart-items">
      <div class="cart-title">
        <h4 class="">YOUR CART</h4>
        <i class="zmdi zmdi-close" @click="toggle"></i>
      </div>
      <table class="cart-table">
        <thead>
          <tr>
            <th>Item</th>
            <th>Price</th>
            <th>Qty</th>
            <th>Total</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(quantity, key, i) in cart" :key="i">
            <td>{{ key }}</td>
            <td>${{ getPrice(key)}}</td>
            <td>{{ quantity }}</td>
            <td>${{ quantity * getPrice (key) }}</td>
            <td>
              <button @click="remove(key)" class="btn">
                &times;
              </button>
            </td>
          </tr>
        </tbody>
      </table>
      <p class="center" v-if="!Object.keys(cart).length"><em>No items in cart</em></p>

