        <div class="item-img">
          <img src="../assets/images/xitem-cart-01.jpg.pagespeed.ic.OR3SDRpeNa.webp" alt="">
        </div>
        <div class="cart-info">
          <span>White Shirt Pleat</span>
          <p>1 x $19.00</p>
        </div>
      </div>
      <div class="cart-item">
        <div class="item-img">
          <img src="../assets/images/xitem-cart-02.jpg.pagespeed.ic.i1QzWnpTT6.webp" alt="">
        </div>
        <div class="cart-info">
          <span>Converse All Star</span>
          <p>1 x $39.00</p>
