import { createApp } from 'vue'
import App from './App.vue'
import router from './router'

/* import the fontawesome core */
import { library } from '@fortawesome/fontawesome-svg-core'
/* import font awesome icon component */
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'
/* import specific icons */
import { faFacebookF, faPinterestP, faInstagram } from '@fortawesome/free-brands-svg-icons'

// All CSS files
import '../public/index.css'
import '../src/assets/css/home.css'
import '../src/assets/css/about.css'
import '../src/assets/css/blog.css'
import '../src/assets/css/features.css'
import '../src/assets/css/contact.css'
import '../src/assets/css/sidebar.css'
import '../src/assets/css/product.css'

/* add icons to the library */
library.add(faFacebookF, faInstagram, faPinterestP)

const app = createApp(App)
app.use(router)
app.component('font-awesome-icon', FontAwesomeIcon)
app.mount('#app')
