<template>
<header>
  <router-link to="/">
    <img src="../public/logo.png" alt="" />
  </router-link>
  <nav class="desktop" v-if="showNav">
    <router-link class="nav-link" to="/">Home</router-link>
        <!-- <router-link to="#">Homepage 1</router-link>
        <router-link to="#">Homepage 2</router-link>
        <router-link to="#">Homepage 3</router-link> -->
    <router-link class="nav-link" to="shop">Shop</router-link>
    <router-link class="nav-link hot" to="features">
      <span>HOT</span>
      <p>Features</p>
    </router-link>
    <router-link class="nav-link" to="blog">Blog</router-link>
    <router-link class="nav-link" to="about">About</router-link>
    <router-link class="nav-link" to="contact">Contact</router-link>
  </nav>
    <div class="header-icons">
      <i class="zmdi zmdi-search icon" @click="toggleHeaderSearch"></i>
      <i class="zmdi zmdi-shopping-cart icon" @click="toggleSideBar"><p>{{ totalQuantity }}</p></i>
      <i class="zmdi zmdi-favorite-outline icon"><p>0</p></i>
      <i class="zmdi zmdi-menu" @click="changeMenuIcon"></i>
      <!-- <i class="zmdi zmdi-close"></i> -->
    </div>
  </header>
  <HeaderSearch
  v-if="showHeaderSearch"
  :toggle="toggleHeaderSearch"
  />
  <router-view :inventory="inventory" :addToCart="addToCart"/>

  <SideBar
    v-if="showSideBar"
    :toggle="toggleSideBar"
    :cart="cart"
    :inventory="inventory"
    :remove="removeItem"
  />

</template>

<script>
import SideBar from '@/components/SideBar.vue'
import HeaderSearch from '@/components/HeaderSearch.vue'
import products from '@/products.json'

export default {
  components: {
    SideBar,
    HeaderSearch
  },
  data () {
    return {
      showSideBar: false,
      showHeaderSearch: false,
      showNav: true,
      inventory: products,
      cart: {}
    }
  },
  computed: {
    totalQuantity () {
      return Object.values(this.cart).reduce((acc, curr) => {
        return acc + curr
      }, 0)
    }
  },
  methods: {
    addToCart (name, quantity) {
      if (!this.cart[name]) this.cart[name] = 0
      this.cart[name] += quantity
    },
    toggleSideBar () {
      this.showSideBar = !this.showSideBar
    },
    toggleHeaderSearch () {
      this.showHeaderSearch = !this.showHeaderSearch
    },
    changeMenuIcon () {
      this.showNav = !this.showNav
      // icon.classList.toggle('zmdi-close')
    },
    removeItem (name) {
      delete this.cart[name]
    }
  }
}
</script>
