<template>
  <div class="products-filter">
    <div class="product-top">
      <div class="button-group">
        <button type="button" class="active">All Products</button>
        <button type="button">Office</button>
        <button type="button">Kitchen</button>
        <button type="button">Resturant</button>
        <button type="button">Bedroom</button>
        <button type="button">living room</button>
      </div>
      <div class="filter">
        <div class="filter-icon" @click="handleFilter">
          <i class="zmdi zmdi-filter-list hover" ></i>
          <p class="hover">Filter</p>
        </div>
        <div class="filter-icon" @click="handleSearch">
          <i class="zmdi zmdi-search hover"></i>
          <p class="hover">Search</p>
        </div>
      </div>
    </div>
    <div class="search" v-if="showSearch">
      <i class="zmdi zmdi-search hover"></i>
      <input type="search" value="" placeholder="Search">
    </div>
    <div class="filter" v-if="showFilter">
      <h1>THis Is The Filter Part</h1>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      showSearch: false,
      showFilter: false
    }
  },
  methods: {
    handleSearch () {
      this.showSearch = !this.showSearch
    },
    handleFilter () {
      this.showFilter = !this.showFilter
    }
  }
}
</script>
<style>
.products-filter {
  padding-left: var(--section-padding);
  padding-right: var(--section-padding);
  padding-bottom: 30px;
}
